.App {
  text-align: center;
  background-color:#FFA500;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.gameSelect {
 background-color: aliceblue; 
 width: 32%;
 height: 100px;
 justify-content: space-between;
 font-size: 20px;
}

#SmartAss{
  background-color: #FFD000; /* Bright yellow */
  color: #E30613;           /* Bold red text */
  font-family: 'Arial Black', sans-serif; /* Playful bold font */
  font-size: 2rem;           /* Large text size */
  text-transform: uppercase; /* Uppercase letters */
  /* padding: 15px 30px;        Padding for size */
  border: 3px solid black;   /* Black border for contrast */
  border-radius: 5px;        /* Slightly rounded corners */
  box-shadow: 5px 5px 0px black; /* 3D shadow effect */
  cursor: pointer;           /* Pointer cursor for hover */
  transition: all 0.2s ease; /* Smooth hover effect */
  line-height: 0.1;
}

#SmartAss:hover {
  background-color: #FFA500; /* Orange shade on hover */
  box-shadow: 3px 3px 0px black; /* Smaller shadow on hover */
  transform: translate(2px, 2px); /* Slight shift */
}

#TPWC, #TP{
  background-color: #021c52;
  font-family:'Arial Black', sans-serif; 
  font-size: 25px;           /* Large text size */
  border: 3px solid rgb(189, 194, 85);   /* Black border for contrast */
  border-radius: 5px;        /* Slightly rounded corners */
  box-shadow: 5px 5px 0px black; /* 3D shadow effect */
  cursor: pointer;           /* Pointer cursor for hover */
  transition: all 0.2s ease; /* Smooth hover effect */
  line-height: 0.1;
  color: rgb(189, 194, 85);
}

#TPWC:hover, #TP:hover {
  background-color: #3d4f78; /* Orange shade on hover */
  box-shadow: 3px 3px 0px black; /* Smaller shadow on hover */
  transform: translate(2px, 2px); /* Slight shift */
}