/* General styles for card container and buttons */
.card-container {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    justify-content: space-between;
    padding: 20px;
    background-color: aliceblue;
  }

  .card-button, .tpwc-card-button {
    width: 200px;
    height: 125px;
    cursor: pointer;
    border-radius: 10px;
    border-color: black;
    -webkit-text-fill-color: white;
    font-size: x-large;
    outline: 0.5px solid black;
    border: solid 5px white;
  }

  .tpwc-card-button{
    background-color: #8d44b6;
  }

  .card-button:hover {
    transform: rotate(0.05turn);
    transition-duration: 1s;
  }

  h1{
    font-size: 40px;
    align-content: center;
  }
  div{
    text-align: center;
  }

.card-button:nth-child(2n):hover { transform: rotate(-0.05turn);
    transition-duration: 1s; }
.card-button:nth-child(3n):hover { transform: rotate(0.1turn);
    transition-duration: 1s; }
.card-button:nth-child(4n):hover { transform: rotate(-0.1turn);
    transition-duration: 1s; }

