.modal {
    position: fixed; /* Use fixed to center it relative to the viewport */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex; /* Enable flexbox for centering */
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.6); /* Dim background overlay */
    z-index: 1000; /* Ensure it's on top of other content */
}

.modal-content {
    width: 350px; /* Slightly wider */
    max-width: 80%; /* Responsive width for smaller screens */
    padding: 20px; /* Add padding inside the modal */
    background: white;
    border-radius: 10px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3); /* Maintain shadow */
    position: relative;
    animation: showModal 0.4s ease-out; /* Keep animation */
    transform-style: preserve-3d; /* Maintain depth styling */
    display: flex;
    flex-direction: column; /* Stack content vertically */
    align-items: center; /* Center align text/buttons inside */
    max-height: 80vh; /* Restrict max height to avoid overflowing the viewport */
    overflow-y: auto; /* Enable vertical scroll for content overflow */
    word-wrap: break-word; /* Ensure long words wrap */
    white-space: pre-wrap; /* Preserve formatting and wrap lines */
}

.modal-content pre {
  white-space: pre-wrap; /* Preserve formatting and wrap long lines */
  word-wrap: break-word; /* Ensure long words wrap */
  width: 100%; /* Use full width inside modal */
  overflow-x: auto; /* Allow horizontal scrolling if needed */
  margin: 10px 0; /* Add margin above and below */
}

.close {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
}
  
